<template>
    <basic-layout>
        <template>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    v-loading="loading"
            >
                <el-table-column
                        prop="id"
                        label="编号"
                        width="50">
                </el-table-column>
                <el-table-column
                        prop="uid"
                        label="用户id"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="cid"
                        sortable
                        label="公司id"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="state"
                        label="状态"
                        width="80"
                        text-align="center"
                >
                </el-table-column>
                <el-table-column
                        label="操作"
                        width="200">
                    <template slot-scope="scope">
                        <el-button type="warning">
                            <router-link :to="{path:'/interviewShow/'+scope.row.id}">查看</router-link>
                        </el-button>
                        <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"

    export default {
        name: "interviewList",
        components: {
            "basic-layout": Layout,
        },
        data() {
            return {
                tableData: [],
                loading: true,
            }
        },
        mounted() {
            this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getInterviews").then(res => {
                console.log(res.data.data);
                let data = res.data.data.map(v => {
                    switch (v.state) {
                        case "1":
                            v.state = "已发送";
                            break;
                        case "2":
                            v.state = "已接收";
                            break;
                        case"3":
                            v.state = "成功回答";
                            break;
                        case "4":
                            v.state = "已查看";
                            break;
                        case "5":
                            v.state = "发来通知";
                            break;
                        case "6":
                            v.state = "完成评价";
                            break;
                        case "7":
                            v.state = "不合适";
                            break;
                        case "8":
                            v.state = "合适";
                            break;
                    }
                    ;
                    return v;
                });
                this.tableData = data;
                this.$message({
                    message: "查询成功",
                    type: "success",
                });
                this.loading = false;

            })

        },
    }
</script>

<style scoped>

</style>


