<template>
    <basic-layout>
        <template>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    v-loading="loading"
            >
                <el-table-column
                        prop="nickName"
                        label="昵称"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="realmame"
                        label="姓名"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="age"
                        sortable
                        label="age"
                        width="50">
                </el-table-column>
                <el-table-column
                        prop="avatar"
                        label="头像"
                        width="80"
                >
                    <template slot-scope="scope">
                        <img :src="scope.row.avatar" alt="" style="width:50px;height:50px;border-radius:50%">
                    </template>
                </el-table-column>
                <el-table-column
                        sortable
                        prop="experience"
                        label="工作经历"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="record"
                        label="学历">
                </el-table-column>
                <el-table-column
                        prop="targetPosition"
                        label="应聘岗位"
                        width="180">
                </el-table-column>
                <el-table-column
                        label="操作"
                        width="200">
                    <template slot-scope="scope">
                        <el-button type="warning">
                            <router-link :to="{path:'/userShow/'+scope.row.uid}">查看</router-link>
                        </el-button>
                        <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-row type="flex" justify="center" style="margin-top:30px;">
                <el-pagination
                        background
                        layout="prev, pager, next"
                        :page-size="2"
                        :current-page.sync="now"
                        :total="total"
                        @current-change="change"
                >
                </el-pagination>
            </el-row>
        </template>
    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"

    export default {
        name: "usersList",
        components: {
            "basic-layout": Layout,
        },
        data() {
            return {
                tableData: [],
                loading: true,
                now:0,
                total:0
            }
        },
        methods: {
            del(id) {
                this.$confirm('确定删除吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/delUser?id=" + id).then(res => {
                        if (res.data.data.state === "success") {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.tableData = this.tableData.filter(ele => ele.uid !== id);
                            console.log(data);
                        } else {
                            this.$message({
                                type: 'error',
                                message: '删除删除失败!'
                            });
                        }


                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            change(){},
            getData(p=1){
                this.$http.get("admin/users?p="+p).then(res => {
                    let data = res.data.data;
                    if (res.status === 200) {
                        this.tableData = data;
                        this.$message({
                            message: "查询成功",
                            type: "success",
                        });
                        this.loading = false;
                    }else{
                        this.$message({
                            message: "查询失败",
                            type: "error",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getData();

        },
    }

</script>

<style scoped>

</style>


