<template>
    <basic-layout>
        <el-form :inline="true" :model="form" class="demo-form-inline" ref="search" :rules="rules">
            <el-form-item label="标题" prop="title">
                <el-input v-model="form.title" placeholder="请输入标题"></el-input>
            </el-form-item>
            <el-form-item label="种类" prop="type">
                <el-select v-model="form.type" placeholder="请输入类型">
                    <el-option label="UI" value="UI"></el-option>
                    <el-option label="WEB" value="WEB"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit()">查询</el-button>
            </el-form-item>
        </el-form>
        <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading"

        >
            <el-table-column
                    sortable
                    prop="title"
                    label="文章主题"
                    width="180">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="author"
                    label="作者姓名"
                    width="110">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="publicDate"
                    label="发布时间"
                    width="180">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="type"
                    label="类型"
                    width="100">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="description"
                    label="文章描述"
                    width="220">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="state"
                    label="审核状态"
                    width="180">
            </el-table-column>

            <el-table-column
                    label="操作"
                    width="120">
                <template slot-scope="scope">
                    <el-button type="primary" plain v-if="scope.row.state==='未审核'" @click="auditwork(scope.row)">点击审核
                    </el-button>
                    <el-button type="success" plain v-else-if="scope.row.state==='通过'" @click="showContent(scope.row)">
                        查看作品
                    </el-button>
                    <el-button type="waning" plain v-else-if="scope.row.state==='未通过'" @click="auditwork(scope.row)">
                        重新审核
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-row type="flex" justify="center" style="margin-top: 30px">
            <el-pagination
                    layout="prev, pager, next"
                    :total="total"
                    :page-size="3"
                    @current-change="change"
                    :current-page.sync="now"
            >
            </el-pagination>
        </el-row>
        <el-dialog
                title="审核"
                :visible.sync="dialogVisible"
                width="50%"
                :before-close="handleClose">
            <div class="title" style="text-align: center;font-weight: 500">{{obj.title}}</div>
            <div class="title">{{obj.content}}</div>
            <div>
                <el-select v-model="value" placeholder="请选择">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>

            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="changeState">确 定</el-button>
  </span>
        </el-dialog>
        <el-dialog
                :visible.sync="showArticle"
                width="50%"
        >
            <div class="title" style="text-align: center;font-weight: 500">{{obj.title}}</div>
            <div class="title">{{obj.content}}</div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="showArticle = false">取 消</el-button>
  </span>
        </el-dialog>
    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"
    import ElRow from "element-ui/packages/row/src/row";

    export default {
        name: "articleLIst",
        components: {
            ElRow,
            "basic-layout": Layout,
        },
        data() {
            return {
                type: "",
                title: "",
                total: 0,
                now: 1,
                tableData: [],
                dialogVisible: false,
                showArticle: false,
                obj: {},
                style: "",
                loading: true,
                options: [
                    {
                        value: 'pass',
                        label: '通过'
                    }, {
                        value: 'failed',
                        label: '未通过'
                    }
                ],
                value: '',
                form: {
                    title: '',
                    type: ''
                },
                rules: {
                    title: [
                        {required: true, message: '请输入作品名称', trigger: 'blur'},
                    ],
                    type: [
                        {required: true, message: '请选择类型', trigger: 'blur'}
                    ],
                }
            }
        },
        methods: {
            auditwork: function (obj) {
                this.obj = obj;
                this.dialogVisible = true;

            },
            changeState: function () {
                console.log(this.value)
                this.dialogVisible = false;
                if (this.value === 'pass') {
                    this.$http.get(`/admin/auditingArticle?aid=${this.obj.aid}&state=${"pass"}`).then(res => {
                        if (res.data.data.message === "ok") {
                            this.$message({
                                message: "修改成功",
                                type: "success"
                            });
                            this.obj.state = "通过"
                        }
                    });
                } else if (this.value === "failed") {
                    this.$http.get(`/admin/auditingArticle?aid=${this.obj.aid}&state=${"pass"}`).then(res => {
                        if (res.data.data.message === "ok") {
                            this.$message({
                                message: "修改成功",
                                type: "success"
                            });
                            this.obj.state = "未通过"
                        }
                    });
                }
                this.value = "";
            },
            showContent(obj) {
                this.obj = obj;
                this.showArticle = true;
            },
            onSubmit() {
                this.$refs.search.validate((valid) => {
                    if (valid) {
                        this.now = 1;
                        this.getData()
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            change(p) {
                this.getData(p)

            },
            getData(p = 1) {
                this.$http.get(`/admin/getArticles?p=${p}&title=${this.form.title}&type=${this.form.type}`).then(res => {
                    this.total = res.body.total;
                    if (res.body.state === "ok") {
                        let data = res.data.data.map(v => {
                            if (v.title.length > 10) {
                                v.title = v.title.slice(0, 10);
                                v.title += "...";
                            }
                            if (v.description.length > 50) {
                                v.description = v.description.slice(0, 50);
                                v.description += "...";
                            }
                            if (v.state === "pass") {
                                v.state = '通过'
                            } else if (v.state === 'fail') {
                                v.state = '未通过'
                            } else if (v.state === "unauditing") {
                                v.state = '未审核'
                            }
                            return v;
                        })
                        this.tableData = data;
                        this.loading = false;
                        this.$message({
                            message: "查询成功",
                            type: "success",
                        })
                    } else {
                        this.$message({
                            message: "查询失败",
                            type: "fail",
                        })
                    }
                })
            }
        },
        mounted: function () {
            this.getData();

        }
    }
</script>

<style scoped>

</style>


