<template>
    <el-container class="container">
        <el-header class="header" style="text-align: center;line-height: 60px;">大学生就业招聘网管理平台</el-header>
        <el-container>
            <el-aside class="aside" width="200px">
                <el-menu
                        :default-active="$route.path"
                        class="el-menu-vertical-demo"
                        background-color="#ccc"
                        :router="true"
                        height="100%">
                    <el-menu-item index="/" :route="{path:'/'}"><i class="el-icon-menu"></i><span slot="title">首页</span>
                    </el-menu-item>
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-location"></i><span>内容管理</span></template>
                        <el-menu-item index="/workslist" :route="{path:'/workslist'}">作品管理</el-menu-item>
                        <el-menu-item index="/articleList" :route="{path:'/articleList'}">文章管理</el-menu-item>
                        <el-menu-item index="/videoList" :route="{path:'/videoList'}">视频管理</el-menu-item>
                        <el-menu-item index="/recruitmentList" :route="{path:'/recruitmentList'}">招聘信息管理</el-menu-item>
                        <el-menu-item index="/resumeList" :route="{path:'/resumeList'}">招聘信息管理</el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-location"></i><span>用户管理</span></template>
                        <el-menu-item index="/usersList" :route="{path:'/usersList'}">个人信息管理</el-menu-item>
                        <el-menu-item index="/companiesList" :route="{path:'/companiesList'}">企业资质认证</el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                    <template slot="title"><i class="el-icon-location"></i><span>面试管理</span></template>
                    <el-menu-item index="/interviewList" :route="{path:'/interviewList'}">面试信息管理</el-menu-item>
                    <el-menu-item index="/interQuestionList" :route="{path:'/interQuestionList'}">面试题目管理</el-menu-item>
                </el-submenu>
                    <el-submenu index="5">
                        <template slot="title"><i class="el-icon-location"></i><span>公告管理</span></template>
                        <el-menu-item index="/noticeList" :route="{path:'/noticeList'}">公告查看</el-menu-item>
                        <el-menu-item index="/noticeAdd" :route="{path:'/noticeAdd'}">公告添加</el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main class="main">
                <slot></slot>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    export default {
        name: "basicLayout",
        props: ["index"]
    }
</script>

<style scoped>
    .container {
        height: 100%;
        background: #cfcfcf;
    }

    .header {
        height: 20px;
        background: #9f9f9f;;
    }

    .aside {

        background: #ccc;
    }

    .main {
        background: #f3f3f3;
    }
</style>


