<template>
    <basic-layout>
        <template>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%;text-align:center;"
                    v-loading="loading"
                    
            >
                <el-table-column
                        sortable
                        prop="id"
                        label="编号"
                        width="80">
                </el-table-column>
                <el-table-column
                        sortable
                        prop="type"
                        label="题型"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="title"
                        label="题目"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="question"
                        label="请回答"
                        width="250">
                </el-table-column>
                <el-table-column
                        label="答案"
                        prop="answer"
                        width="120">
                </el-table-column>
            </el-table>
        </template>
    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"

    export default {
        name: "interQuestionList",
        components: {
            "basic-layout": Layout,
        },
        data() {
            return {
                tableData: [],
            }
        },
        mounted() {
            this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getSubjects").then(res => {
                console.log(res.data.data);
                let data = res.data.data;
                for(let i=0;i<data.length;i++){
                    data[i]["question"]="";
                    if(data[i].optionA===""){
                        data[i]["question"]="";

                    }else{
                        data[i]["question"] ="A :"+data[i].optionA+"B :"+data[i].optionB+"C :"+data[i].optionC+"D : "+data[i].optionD;
                    }
                }
                this.tableData = data;
                console.log(this.tableData);
                this.$message({
                    message: "查询成功",
                    type: "success",
                });
                this.loading = false;

            })

        },
        methods: {
            open4() {
                const h = this.$createElement;
                this.$msgbox({
                    title: '消息',
                    message: h('p', null, [
                        h('span', null, '内容可以是 '),
                        h('i', {style: 'color: teal'}, 'VNode')
                    ]),
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 3000);
                        } else {
                            done();
                        }
                    }
                }).then(action => {
                    this.$message({
                        type: 'info',
                        message: 'action: ' + action
                    });
                });
            }
        }
    }
</script>

<style scoped>

</style>


