<template>
    <basic-layout>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/interviewList' }">面试管理</el-breadcrumb-item>
            <el-breadcrumb-item>活动列表</el-breadcrumb-item>
            <el-breadcrumb-item>活动详情</el-breadcrumb-item>
        </el-breadcrumb>
    <p>招聘公司：&nbsp&nbsp&nbsp&nbsp{{companyName}}</p>
        <p>求职者：&nbsp&nbsp&nbsp&nbsp{{userName}}</p>

    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"
    export default {
        name: "interviewShow",
        components: {
            "basic-layout": Layout,
        },
        data(){
            return{
                data:[],
                loading:true,
               companyName:"",
                userName:"",
            }
        },
        mounted() {
            let id=this.$route.params.id;
            console.log(id);
            this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getInterview?id="+id).then(res => {
                let data=res.data.data;
                let cid=data.cid;
                let uid=data.uid;
                this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getCompany?cid="+cid).then(res => {
                    let data = res.data.data;
                    this.companyName=data.name;
                })
                this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getUser?uid="+uid).then(res => {
                    let data = res.data.data;
                    this.userName=data.realName;
                    console.log(data.realName);
                })


            })

        },
    }
</script>

<style scoped>

</style>


